* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

a {
  color: inherit;
}

a,
button {
  outline: none;
}
a:focus-visible,
button:focus-visible {
  outline: 3px solid #333;
  outline-offset: 2px;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "Roboto", sans-serif;
  color: #fff;
  font-weight: 400;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "Roboto", sans-serif;
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }
}

.main {
  flex: 1 1 auto;
  padding-top: 164px;
}
@media (max-width: 992px) {
  .main {
    padding-top: 156px;
  }
}
@media (max-width: 768px) {
  .main {
    padding-top: 130px;
  }
}
.main__about {
  padding: 30px 0 35px 0;
}
@media (max-width: 600px) {
  .main__about {
    padding: 20px 0;
  }
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 200;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease 0s;
  opacity: 1;
  visibility: visible;
}
.preloader.done {
  opacity: 0;
  visibility: hidden;
}

.btn {
  cursor: pointer;
  display: inline-flex;
  padding: 0 25px;
  justify-content: center;
  align-items: center;
  height: 39px;
  color: #fff;
  background-color: #1ab9ce;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 900;
  transition: all 0.3s ease 0s;
}
@media (any-hover: hover) {
  .btn:hover {
    background-color: #1494a5;
  }
}

.side-text {
  font-size: 12px;
  line-height: 150%;
  font-weight: 300;
  color: #666;
  transition: color 0.3s ease 0s;
}

.side-label {
  font-size: 12px;
  line-height: 150%;
  font-weight: 900;
  text-transform: uppercase;
  color: #333;
  transition: color 0.3s ease 0s;
}

.item-product {
  padding: 15px 25px;
  position: relative;
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease 0s;
  border: 5px solid #f2f2f2;
}
.item-product_hover {
  overflow: hidden;
  border: 2px solid #f2f2f2;
}
@media (any-hover: hover) {
  .item-product_hover:hover .item-product__hover {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
  .item-product_hover:hover .item-product__name,
.item-product_hover:hover .item-product__footer,
.item-product_hover:hover .item-product__btns {
    opacity: 0;
    visibility: hidden;
  }
}
.item-product__label {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
  font-size: 12px;
  font-weight: 900;
  padding: 0 15px;
  height: 21px;
  text-transform: uppercase;
  background-color: #1ab9ce;
  color: #fff;
  border-radius: 20px;
}
.item-product__image {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 10px 0;
  margin-bottom: 10px;
  transition: all 0.3s ease 0s;
  width: 160px;
  height: 175px;
}
.item-product__name {
  transition: all 0.3s ease 0s;
  color: #ee2c2d;
  margin-bottom: 16px;
  flex: 1 1 auto;
  opacity: 1;
  visibility: visible;
}
@media (any-hover: hover) {
  .item-product__name:hover {
    color: #962103;
  }
}
.item-product__title {
  display: inline-block;
  font-weight: 900;
  font-size: 16px;
  line-height: 120%;
}
.item-product__subtitle {
  font-weight: 300;
}
.item-product__footer, .item-product__btns {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease 0s;
  display: flex;
  align-items: center;
}
.item-product__footer {
  justify-content: space-between;
  flex-wrap: wrap;
}
.item-product__btns {
  margin-bottom: 30px;
  justify-content: space-evenly;
}
.item-product__btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 39px;
  height: 39px;
  border-radius: 50%;
  background-color: #ee2c2d;
  transition: all 0.3s ease 0s;
}
.item-product__btn::before {
  font-size: 15px;
  font-weight: 700;
  color: #fff;
}
@media (any-hover: hover) {
  .item-product__btn:hover {
    transform: scale(1.1);
  }
}
.item-product__price {
  color: #333;
}
.item-product__hover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-110%);
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease 0s;
}

.hover-item {
  height: 100%;
  background-color: rgba(0, 0, 0, 0.84);
  border: 5px solid #1ab9ce;
  padding: 20px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1200px) {
  .hover-item {
    padding: 12px;
  }
}
.hover-item__title {
  display: block;
  font-size: 16px;
  line-height: 130%;
  font-weight: 300;
  color: #dadada;
  margin-bottom: 25px;
}
.hover-item__title span {
  font-weight: 900;
  color: #fff;
  display: block;
}
@media (max-width: 1200px) {
  .hover-item__title {
    font-size: 15px;
    margin-bottom: 15px;
  }
}
.hover-item__descr {
  font-size: 14px;
  line-height: 130%;
  font-weight: 400;
  color: #fff;
  margin-bottom: 20px;
}
.hover-item__descr span {
  display: block;
  font-weight: 900;
  color: #1ab9ce;
}
.hover-item__descr span:not(:last-child) {
  margin-bottom: 5px;
}
@media (max-width: 1200px) {
  .hover-item__descr {
    margin-bottom: 30px;
  }
}
.hover-item__btns {
  flex: 1 1 auto;
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 20px;
}
.hover-item__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 52px;
  width: 52px;
  background-color: #1ab9ce;
  border-radius: 50%;
  color: #fff;
  font-size: 20px;
  transition: all 0.3s ease 0s;
}
@media (any-hover: hover) {
  .hover-item__btn:hover {
    transform: scale(1.1);
  }
}
.hover-item__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.hover-item__label {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  color: #1ab9ce;
  border: 1px solid #1ab9ce;
  border-radius: 10px;
  font-size: 16px;
  line-height: 180%;
  font-weight: 300;
  padding: 0 12px;
}
.hover-item__price {
  color: #fff;
}
@media (max-width: 1200px) {
  .hover-item__price {
    font-size: 22px;
  }
}

.product-old-price {
  color: #666;
  font-size: 18px;
  font-weight: 300;
  text-decoration: line-through;
}

.product-price {
  font-size: 24px;
  font-weight: 900;
}

.title {
  font-family: "UniSans-Trial";
  font-size: 30px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.025em;
  display: inline-block;
}

.choices {
  border: 1px solid #dadada;
  transition: border-color 0.3s ease;
}
.choices.is-focused {
  border-color: #333;
}
.choices.is-focused .choices__list--dropdown {
  border-color: #333;
}

.choices__list--dropdown .choices__item--selectable, .choices__list[aria-expanded] .choices__item--selectable {
  padding-right: 0;
}

.choices[data-type*=select-one].is-open::after {
  margin-top: 0;
  transform: rotate(-180deg);
}

.choices[data-type*=select-one]::after {
  border: none;
  top: 40%;
  background: url("../resources/icons/icons/caret-down-solid.svg") center/cover no-repeat;
  height: 10px;
  width: 12px;
  fill: #666;
  transition: transform 0.3s ease, margin-top 0.3s ease;
}

.choices__inner {
  margin: 0;
  padding: 0;
  padding-bottom: 0;
  align-items: flex-end;
  min-height: 30px;
  background-color: #fff;
}

.choices__list--single {
  padding: 7px 7px 0 7px;
}

.choices__item {
  padding-right: 0;
  width: 100%;
  color: #ee2c2d;
  font-size: 14px;
  font-weight: 600;
  transition: background-color 0.3s ease;
}
.choices__item.is-highlighted {
  background-color: #dadada;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted::after {
  display: none;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
.tab {
  cursor: pointer;
  display: inline-flex;
  padding: 0 15px;
  height: 32px;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 14px;
  background-color: #fff;
  color: #ee2c2d;
  border-radius: 20px;
  border: 1px solid #ee2c2d;
}
.tab.active {
  background-color: #ee2c2d;
  color: #fff;
  border: 1px solid #ee2c2d;
}

.flyImage {
  opacity: 1;
  z-index: 100;
  position: fixed;
  transition: all 1.5s ease 0s;
}

.add-btn.hold {
  opacity: 0.2;
  cursor: default;
}

.quantity {
  display: flex;
  align-items: center;
}
.quantity__btn {
  color: #dadada;
  font-size: 22px;
  transition: all 0.3s ease 0s;
}
@media (any-hover: hover) {
  .quantity__btn:hover {
    color: #ee2c2d;
  }
}
@media (max-width: 992px) {
  .quantity__btn {
    color: #ee2c2d;
  }
}
.quantity__btn.disabled {
  pointer-events: none;
  color: #dadada;
}
.quantity__current-number {
  flex: 0 0 56px;
  height: 33px;
  border: 2px solid #dadada;
  margin: 0 10px;
}
.quantity__input {
  width: 100%;
  height: 100%;
  outline: none;
  padding: 0 5px;
  text-align: center;
  font-size: 16px;
  font-weight: 900;
  color: #666;
}
.quantity input::-webkit-outer-spin-button,
.quantity input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox {
  appearance: none;
}
.checkbox:checked + .checkbox-tag::after {
  transform: scale(1);
}
.checkbox:focus + .checkbox-tag {
  outline: 2px solid #333;
  outline-offset: 3px;
}
.checkbox input::-webkit-outer-spin-button,
.checkbox input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox-tag {
  position: relative;
  display: inline-block;
}
.checkbox-tag::before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: 15px;
  height: 15px;
  border: 2px solid #ee2c2d;
}
.checkbox-tag::after {
  transition: all 0.3 ease 0s;
  position: absolute;
  content: "";
  top: 1px;
  left: 3px;
  width: 9px;
  height: 9px;
  background-color: #ee2c2d;
  transform: scale(0);
}

.breadcrumbs__list {
  display: flex;
  align-items: center;
}
.breadcrumbs__item {
  display: flex;
  align-items: center;
  margin-right: 10px;
  font-size: 14px;
  font-weight: 300;
  color: #dadada;
}
.breadcrumbs__item:last-child {
  margin-right: 0;
}
.breadcrumbs__link {
  display: block;
  color: #ee2c2d;
  transition: all 0.3s ease 0s;
  margin-right: 10px;
}
@media (any-hover: hover) {
  .breadcrumbs__link:hover {
    color: #962103;
  }
}
.breadcrumbs__icon {
  color: #ee2c2d;
  font-size: 12px;
}

.search-result__title {
  color: #dadada;
  margin-bottom: 15px;
}
.search-result__list {
  color: #ee2c2d;
  padding: 0 25px;
  margin-bottom: 25px;
}
.search-result__item:not(.search-result__item:last-child) {
  margin-bottom: 10px;
}
.search-result__link {
  color: #ee2c2d;
  font-size: 18px;
  font-weight: 700;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .search-result__link:hover {
    color: #962103;
  }
}
@media (max-width: 480px) {
  .search-result__link {
    font-size: 14px;
  }
}
.search-result__false {
  padding: 0 5px;
  color: #ee2c2d;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 25px;
}

.js-validate-error-field {
  border: none;
  box-shadow: 0 0 8px 1px #ee2c2d;
}

.js-validate-error-label {
  padding-top: 4px;
}

.policy {
  padding: 10px;
  background-color: #ee2c2d;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
}
.policy p {
  line-height: 120%;
  margin-bottom: 10px;
}

