.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
}
.header.active {
  box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.2);
}
.header__top {
  position: relative;
  z-index: 5;
}
.header__top::before {
  content: "";
  position: absolute;
  width: 240px;
  height: 190px;
  bottom: -40px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #ee2c2d;
  border-radius: 100%;
  z-index: 0;
}
@media (max-width: 992px) {
  .header__top::before {
    display: none;
  }
}

.top-header {
  background-color: #ee2c2d;
}
.top-header__container {
  display: flex;
  align-items: center;
  height: 90px;
}
@media (max-width: 768px) {
  .top-header__container {
    height: 70px;
  }
}
.top-header__column {
  flex: 0 1 50%;
}
.top-header__column_logo {
  z-index: 5;
  padding-top: 30px;
  flex: 0 0 190px;
  text-align: center;
  align-self: flex-start;
}
@media (max-width: 992px) {
  .top-header__column_logo {
    padding-top: 0;
    align-self: center;
  }
}
.top-header__menu {
  position: relative;
}
@media (max-width: 992px) {
  .top-header__menu {
    display: flex;
    align-items: center;
    padding: 0 35px 0 10px;
  }
}
@media (max-width: 768px) {
  .top-header__menu {
    padding: 0 0 0 10px;
  }
}
.top-header__burger {
  display: none;
}
@media (max-width: 992px) {
  .top-header__burger {
    margin-right: auto;
    position: relative;
    display: block;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 10;
  }
  .top-header__burger span {
    display: block;
    position: absolute;
    top: calc(50% - 1px);
    left: 0;
    height: 2px;
    width: 100%;
    background-color: #fff;
    transition: all 0.3s ease 0s;
  }
  .top-header__burger span:first-child {
    top: 0;
  }
  .top-header__burger span:last-child {
    top: auto;
    bottom: 0;
  }
  .top-header__burger.active span {
    transform: scale(0);
  }
  .top-header__burger.active span:first-child {
    transform: rotate(45deg);
    top: calc(50% - 1px);
  }
  .top-header__burger.active span:last-child {
    transform: rotate(-45deg);
    bottom: calc(50% - 1px);
  }
}
@media (max-width: 992px) {
  .top-header__body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    transform: translateX(-100%);
    overflow: auto;
    background-color: #ee2c2d;
    z-index: 5;
    padding: 120px 20px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease 0s;
  }
  .top-header__body.active {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: all 0.5s ease 0s;
  }
}
@media (max-width: 992px) and (max-width: 992px) {
  .top-header__body.active .top-header__item {
    animation-name: header-menu;
    animation-delay: 0s;
  }
  .top-header__body.active .top-header__item:nth-child(1) {
    animation-duration: 1.5s;
  }
  .top-header__body.active .top-header__item:nth-child(2) {
