.tooltip {
  display: none;
}
@media (any-hover: hover) {
  .tooltip {
    display: inline-block;
    position: relative;
    margin-left: 10px;
  }
}
.tooltip__btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  font-size: 14px;
  box-shadow: 2px 4px 10px rgba(0, 13, 84, 0.2);
  background: #fff;
}
.tooltip__btn:hover + .tooltip__text {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.tooltip__btn:focus + .tooltip__text {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.tooltip__text {
  position: absolute;
  left: -10px;
  bottom: calc(100% + 14px);
  z-index: 3;
  border-radius: 4px;
  padding: 10px;
  width: max-content;
  max-width: 426px;
  box-shadow: 4px 8px 20px rgba(0, 13, 84, 0.2);
  font-size: 16px;
  text-transform: initial;
  font-weight: 300;
  color: #666;
  background: #fff;
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
.tooltip__text::after {
  content: "";
  position: absolute;
  left: 10px;
  bottom: -8px;
  border-style: solid;
  border-width: 10px 8px 0;
  border-color: #fff transparent transparent transparent;
  border-radius: 4px;
  width: 0;
  height: 0;
}

